import React, { useEffect, useState } from 'react';

const ProductList = ({ onAddToCart }) => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('products.json')
      .then(res => res.json())
      .then(data => {
        setProducts(data);
        setLoading(false);
      });
  }, []);

  if (loading) return <p>Loading products...</p>;

  return (
    <div style={{
      display: 'grid',
      gridTemplateColumns: 'repeat(auto-fit, minmax(220px, 1fr))',
      gap: '2rem',
      margin: '2rem 0'
    }}>
      {products.map(product => (
        <div key={product.id} className="product-card">
          <img src={process.env.PUBLIC_URL + '/' + product.thumbnail} alt={product.title} style={{ width: 200, height: 200, objectFit: 'cover', borderRadius: 16, marginBottom: 16, boxShadow: '0 2px 8px rgba(0,0,0,0.07)' }} />
          <h3>{product.title}</h3>
          <p>â‚¹{product.price}</p>
          <button onClick={() => onAddToCart(product)}>
            Add to Cart
          </button>
        </div>
      ))}
    </div>
  );
};

export default ProductList;
